
.case-study-ucat {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  font-size: 30px;
}
.case-study-ucat .slide-img__shape {
    display: block;
    position: absolute;
    z-index: -100;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, -100px);
}
@media screen and (max-width: 1500px) {
.case-study-ucat .slide-img__shape {
        max-width: 300px;
}
}
.case-study-ucat .slide__button {
    padding: .9em 2em;
    border-style: solid;
    border-width: 1px;
    border-radius: 3px;
    display: inline-block;
    position: relative;
    font-family: 'Oswald', sans-serif;
    text-decoration: none;
    font-size: 1em;
    letter-spacing: .09em;
    text-transform: uppercase;
    color: #EBD6C5;
    overflow: hidden;
    cursor: pointer;
}
.case-study-ucat .case-study__row-button {
    margin: auto;
    justify-content: center;
}
.case-study-ucat .case-study__half-block {
    margin: 0 1em;
}
.case-study-ucat .case-study__line {
    margin: 10em 0;
    color: #EBD6C5;
}
.case-study-ucat .case-study__content {
    color: #524437;
}
.case-study-ucat .case-study-content-background {
    background-color: #EBD6C5;
}
.case-study-ucat .case-study__infos {
    background-color: #debb9f;
    border-color: #d1a079;
}
.case-study-ucat .case-study__section-title:after {
    background-color: #524437;
}
.case-study-ucat .case-study__ucat {
    font-size: 30px;
}
.case-study-ucat .case-study__ucat .case-study__photo-background {
      background-color: #263139;
}
.case-study-ucat .case-study__ucat .case-study__photo {
      margin-top: 100px;
}
.case-study-ucat .case-study__ucat .case-study__section {
      margin: 5em 0;
}
.case-study-ucat .case-study__ucat .case-study__screen-circle img {
      width: 350px;
}



