
.next-button {
  color: #ffffff;
  cursor: pointer;
  position: relative;
  width: 100%;
  padding: 1em 0em;
  font-size: 70px;
  display: block;
  text-align: center;
  background-image: linear-gradient(-30deg, #FFCAD5 -10%, #AEE2FF 100%);
}
.next-button__title {
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  letter-spacing: .05em;
  position: relative;
  padding: 0em .1em;
}
.next-button__subtitle {
  font-family: 'Source Code Pro', monospace;
  font-size: .3em;
  font-weight: 300;
  letter-spacing: .05em;
  margin-bottom: .1em;
}
.next-button__line {
  width: 90%;
  height: 5px;
  opacity: 0;
  position: absolute;
  left: 5%;
  top: 100%;
  background-color: #ffffff;
  transform: translate3d(0px, -50%, 0px) scaleX(0);
  transform-origin: center;
}
.next-button__background {
  background-image: linear-gradient(-30deg, #FFCAD5 -10%, #AEE2FF 100%);
  width: 100%;
  height: 100vh;
  bottom: 0px;
  left: 0px;
  transform: scaleY(0);
  transform-origin: bottom;
  position: absolute;
  z-index: 9;
}
.next-button__content {
  position: relative;
}



