
.page-about {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-title {
  font-family: 'Oswald', sans-serif;
  font-size: 4em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  transform: rotate(-90deg);
  position: absolute;
  top: 100px;
  left: -30px;
}



