
.on-mobile {
  position: absolute;
  z-index: 1000;
  top: 0px;
  height: 0px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(-30deg, #FFCAD5 -10%, #AEE2FF 100%);
  display: none;
  overflow: scroll;
}
@media screen and (max-width: 960px) {
.on-mobile {
      display: block;
}
}



