
.case-study-pm {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  font-size: 30px;
}
.case-study-pm .case-study__content {
    color: #3A2523;
}
.case-study-pm .case-study-content-background {
    background-color: #BAA8A6;
}
.case-study-pm .case-study__infos {
    background-color: #a48c89;
    border-color: #8d706d;
}
.case-study-pm .case-study__section-title:after {
    background-color: #3A2523;
}
.case-study-pm .case-study__text-content-button {
    display: inline-block;
    text-align: center;
    color: #BAA8A6;
    z-index: 1;
    font-weight: 300;
}
.case-study-pm .slide__button {
    padding: .9em 2em;
    border-style: solid;
    border-width: 1px;
    border-radius: 3px;
    display: block;
    position: relative;
    font-family: 'Oswald', sans-serif;
    text-decoration: none;
    font-size: 1em;
    letter-spacing: .09em;
    text-transform: uppercase;
    color: #BAA8A6;
    overflow: hidden;
    cursor: pointer;
}
.case-study-pm .case-study__row-button {
    margin: auto;
    justify-content: center;
}
.case-study-pm .case-study__pepsi {
    font-size: 30px;
}
.case-study-pm .case-study__pepsi .case-study__line {
      margin: 10em 0;
}
.case-study-pm .case-study__pepsi .case-study__section {
      margin: 5em 0;
}
.case-study-pm .case-study__pepsi .case-study__img-cap {
      width: 700px;
}
.case-study-pm .case-study__pepsi .case-study__video-row {
      background-color: #3A2523;
      margin: 0;
      transform: translateX(-13%);
}
.case-study-pm .case-study__pepsi .case-study__photo-background,
    .case-study-pm .case-study__pepsi .case-study__screen-circle {
      background-color: #3A2523;
}
.case-study-pm .case-study__pepsi .case-study__screen-circle img {
      height: 500px;
}



