
@charset "UTF-8";
.about-content {
  margin: auto;
  font-size: 2em;
  text-align: center;
  color: #ffffff;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about-content__title {
  text-align: left;
  letter-spacing: .12em;
}
.about-content__big-title {
  font-size: 3em;
  text-transform: uppercase;
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
  font-style: normal;
  letter-spacing: .12em;
  display: inline-block;
  vertical-align: middle;
  margin: 0px 5px;
  background-color: #425661;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.about-content__content {
  line-height: 1.8em;
  font-size: 2em;
  margin: 2.5em 0em;
}
.about-content__content-container {
  margin: 0em auto;
  font-size: 12px;
  max-width: 65em;
  text-align: left;
}
.about-content__link {
  font-style: bold;
  text-decoration: none;
  color: #6dccfd;
  transition: color .4s ease;
}
.about-content__link:hover {
    color: #e5f6ff;
}
.about-content__mobile-message {
  display: none;
  font-size: 14px;
  margin-top: 3em;
  text-transform: uppercase;
  letter-spacing: .2em;
  line-height: 1.5em;
  color: #ffffff;
}
.about-content__social-title {
  font-size: 1.5em;
  margin-bottom: .6em;
  letter-spacing: .1em;
  color: #6dccfd;
}
.about-content__network {
  display: inline-block;
}
.about-content__network:after {
    content: '•';
    color: #6dccfd;
    margin: 0em .5em;
}
.about-content__network:last-child:after {
    display: none;
    content: '';
}
.about-content__network__link {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  color: #6dccfd;
  text-transform: uppercase;
  letter-spacing: .01em;
  font-size: 3em;
  line-height: 1.5em;
  transition: all .4s ease;
}
.about-content__network__link:hover {
    color: #e5f6ff;
}
@media screen and (min-width: 760px) and (max-width: 960px) {
.about-content__social-title {
    font-size: 13px;
}
.about-content__mobile-message {
    display: block;
}
.about-content {
    font-size: 12px;
    margin: 3em 3em;
    text-align: left;
    max-width: none;
    display: block;
    visibility: visible;
    opacity: 1;
}
.about-content__content-container {
    font-size: 10px;
}
.about-content__big-title {
    font-size: 60px;
}
.about-content__title {
    font-size: 20px;
    letter-spacing: .08em;
    line-height: 3em;
}
}
@media screen and (max-width: 760px) {
.about-content__social-title {
    font-size: 13px;
}
.about-content__mobile-message {
    display: block;
}
.about-content {
    font-size: 10px;
    margin: 3em 2em;
    text-align: left;
    max-width: none;
    display: block;
    visibility: visible;
    opacity: 1;
}
.about-content__content-container {
    font-size: 10px;
}
.about-content__content {
    line-height: 1.3em;
}
.about-content__big-title {
    font-size: 40px;
}
.about-content__title {
    font-size: 19px;
    letter-spacing: .02em;
    line-height: 2em;
}
}
@media screen and (min-width: 961px) and (max-width: 1300px) {
.about-content__social-title {
    font-size: 14px;
}
.about-content {
    max-width: 29em;
}
.about-content__content-container {
    font-size: 10px;
    max-width: 40em;
}
.about-content__big-title {
    font-size: 50px;
}
.about-content__title {
    font-size: 20px;
    line-height: 3em;
}
}
@media screen and (max-width: 340px) {
.about-content__big-title {
    font-size: 40px;
}
}
@media screen and (max-width: 960px) {
.about-content__content-container {
    max-width: none;
}
}



