
.case-study-gh {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  font-size: 30px;
}
.case-study-gh .case-study__content {
    color: #395440;
}
.case-study-gh .case-study-content-background {
    background-color: #E1FFD4;
}
.case-study-gh .case-study__infos {
    background-color: #bdffa1;
    border-color: #9aff6e;
}
.case-study-gh .case-study__section-title:after {
    background-color: #395440;
}
.case-study-gh .case-study__gh {
    font-size: 30px;
}
.case-study-gh .case-study__gh .case-study__photo-background {
      background-color: #020033;
}
.case-study-gh .case-study__gh .case-study__photo {
      margin-top: 100px;
}
.case-study-gh .case-study__gh .case-study__video-row {
      background-color: #020033;
      transform: translateX(-50%);
}
.case-study-gh .case-study__gh .case-study__line {
      margin-top: 5em;
}
.case-study-gh .case-study__gh .case-study__screen-circle img {
      width: 350px;
}



