
.menu {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}
.menu-hit {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 100px;
  height: 110px;
  cursor: pointer;
  z-index: 11;
}
.menu-icon__container {
  position: relative;
  float: right;
  width: 100px;
  height: 100px;
  z-index: 10;
}
.menu-icon {
  width: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.menu-line {
  width: 100%;
  height: 5px;
  background-color: #ffffff;
  margin-bottom: 11px;
  transform-origin: left;
}
.menu-line:last-child {
    margin-bottom: 0px;
}
.menu--light {
  color: #ffffff;
}
.menu-circle-hover {
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  opacity: 0;
  border-radius: 50%;
  transform-origin: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.menu-background {
  width: 100%;
  height: 100%;
  z-index: 9;
  position: absolute;
  visibility: hidden;
  opacity: 0;
  top: 0;
  left: 0;
  background-color: rgba(1, 24, 50, 0.9);
}
.menu-links {
  position: absolute;
  width: 100%;
  text-align: center;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  visibility: hidden;
}



