
.case-study-cs {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  font-size: 30px;
}
.case-study-cs .case-study__content {
    color: #283946;
}
.case-study-cs .case-study__screen-circle {
    background-color: #283946;
}
.case-study-cs .case-study-content-background {
    background-color: #AABAC6;
}
.case-study-cs .case-study__infos {
    background-color: #8ba1b2;
    border-color: #6d889d;
}
.case-study-cs .case-study__section-title:after {
    background-color: #283946;
}
.case-study-cs .case-study__half-block {
    margin: 0 1em;
    transform: translateX(-10%);
}
.case-study-cs .case-study__champs {
    font-size: 30px;
}
.case-study-cs .case-study__champs .case-study__photo-background {
      background-color: #283946;
}
.case-study-cs .case-study__champs .case-study__photo-background-container {
      margin: 0;
}
.case-study-cs .case-study__champs .case-study__photo {
      margin-top: 100px;
}
.case-study-cs .case-study__champs .case-study__video-row {
      background-color: #151f26;
      transform: translateX(-50%);
}
.case-study-cs .case-study__champs .case-study__half-block.case-study__video-row {
      padding: 2em;
      margin-left: 25%;
      width: 60%;
      transform: translateX(0%);
}
.case-study-cs .case-study__champs .case-study__line {
      margin-top: 8em;
}
.case-study-cs .case-study__champs .case-study__section {
      margin: 5em 0;
}
.case-study-cs .case-study__champs .case-study__screen-circle img {
      height: 500px;
}



