
.case-study-vg {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  font-size: 30px;
}
.case-study-vg .case-study__content {
    color: #7F6A3C;
}
.case-study-vg .case-study-content-background {
    background-color: #FFEBC0;
}
.case-study-vg .case-study__infos {
    background-color: #ffdb8d;
    border-color: #ffcb5a;
}
.case-study-vg .case-study__section-title:after {
    background-color: #7F6A3C;
}
.case-study-vg .case-study__vidgeo {
    font-size: 30px;
}
.case-study-vg .case-study__vidgeo .case-study__photo-background {
      background-color: #7F6A3C;
}
.case-study-vg .case-study__vidgeo .case-study__photo {
      margin-top: 100px;
}
.case-study-vg .case-study__vidgeo .case-study__section {
      margin: 5em 0;
}



