
.case-study__video {
  width: 100%;
  font-size: 30px;
  display: block;
  position: relative;
  background-color: #03162A;
  z-index: 1;
}
.case-study__video-row {
  width: 100%;
  padding: 5em;
  background-color: #03162a;
  margin-left: 50%;
  transform: translateX(-13%);
}
.case-study__video-half-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.case-study__video-half {
  width: 40%;
}



